import React, { useState, useEffect } from "react";
import {
  Box,
  Typography,
  TextField,
  Button,
  Grid,
  Paper,
  MenuItem,
  Select,
} from "@mui/material";
import { styled } from "@mui/material/styles";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const StyledPaper = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(4),
  margin: "auto",
  maxWidth: 600,
  marginTop: theme.spacing(8),
  textAlign: "center",
}));

const AppointmentPage = ({ isAuthenticated }) => {
  const navigate = useNavigate();
  const [formData, setFormData] = useState({
    doctorId: "",
    patientEmail: "", // Giri yapm kullancnn email'i
    appointmentDate: "",
    status: "ONAYLANDI",
  });
  const [doctors, setDoctors] = useState([]);
  const [error, setError] = useState("");
  const [success, setSuccess] = useState("");

  useEffect(() => {
    if (!isAuthenticated) {
      navigate("/login");
      return;
    }
    // Giri yapm kullancnn email'ini localStorage veya context'ten alabiliriz (imdilik sabit)
    const userEmail = "dogukan@example.com"; // Gerek projede dinamik olmal
    setFormData((prev) => ({ ...prev, patientEmail: userEmail }));

    // Doktorlar backend'den ek
    axios
      .get("http://localhost:8080/rest/api/doctor/getAllDoctors") // Doktor listesi endpoint'i
      .then((response) => setDoctors(response.data))
      .catch((err) => setError("Doktorlar yklenirken hata olutu."));
  }, [isAuthenticated, navigate]);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    if (!formData.doctorId || !formData.appointmentDate) {
      setError("Ltfen tm zorunlu alanlar doldurun.");
      return;
    }

    axios
      .post("http://localhost:8080/rest/api/appointment/create", formData)
      .then((response) => {
        setSuccess("Randevu baaryla oluturuldu!");
        setTimeout(() => setSuccess(""), 3000); // 3 saniye sonra mesaj temizle
        setFormData({
          doctorId: "",
          patientEmail: formData.patientEmail,
          appointmentDate: "",
          status: "ONAYLANDI",
        });
      })
      .catch((error) => {
        setError(
          "Randevu oluturma baarsz: " +
            (error.response?.data?.message || error.message)
        );
        console.error("Error:", error);
      });
  };

  return (
    <Box sx={{ flexGrow: 1 }}>
      <StyledPaper elevation={3}>
        <Typography variant="h4" gutterBottom>
          Randevu Olutur
        </Typography>
        {error && (
          <Typography color="error" gutterBottom>
            {error}
          </Typography>
        )}
        {success && (
          <Typography color="success.main" gutterBottom>
            {success}
          </Typography>
        )}
        <form onSubmit={handleSubmit}>
          <Grid container spacing={2}>
            <Grid item xs={12}>
              <Select
                fullWidth
                name="doctorId"
                value={formData.doctorId}
                onChange={handleChange}
                displayEmpty
                required
              >
                <MenuItem value="" disabled>
                  Doktor Sein
                </MenuItem>
                {doctors.map((doctor) => (
                  <MenuItem key={doctor.id} value={doctor.id}>
                    {doctor.firstName} {doctor.lastName}
                  </MenuItem>
                ))}
              </Select>
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Hasta E-posta"
                name="patientEmail"
                value={formData.patientEmail}
                onChange={handleChange}
                disabled
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Randevu Tarihi (YYYY-MM-DDTHH:MM)"
                name="appointmentDate"
                type="datetime-local"
                value={formData.appointmentDate}
                onChange={handleChange}
                InputLabelProps={{ shrink: true }}
                required
              />
            </Grid>
            <Grid item xs={12}>
              <TextField
                fullWidth
                label="Durum"
                name="status"
                value={formData.status}
                onChange={handleChange}
                disabled
                required
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                type="submit"
                variant="contained"
                color="primary"
                fullWidth
                sx={{ padding: 1.5 }}
              >
                Randevu Olutur
              </Button>
            </Grid>
          </Grid>
        </form>
      </StyledPaper>
    </Box>
  );
};

export default AppointmentPage;
