import React, { useState } from "react";
import { Link } from "react-router-dom";
import {
  TextField,
  Button,
  Typography,
  Container,
  Box,
  Alert,
} from "@mui/material";

const LoginPage = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [error, setError] = useState(null);

  const handleLogin = (e) => {
    e.preventDefault();
    setError(null); // Hata mesajn temizle

    // Spring Security form login ile ilenecek
    const form = document.createElement("form");
    form.method = "POST";
    form.action = "http://localhost:8080/rest/api/login"; // Doru backend adresi
    const emailInput = document.createElement("input");
    emailInput.type = "hidden";
    emailInput.name = "username"; // Spring Security username bekler
    emailInput.value = email;
    const passwordInput = document.createElement("input");
    passwordInput.type = "hidden";
    passwordInput.name = "password"; // Spring Security password bekler
    passwordInput.value = password;
    form.appendChild(emailInput);
    form.appendChild(passwordInput);
    document.body.appendChild(form);
    form.submit();
  };

  return (
    <Container maxWidth="sm">
      <Box sx={{ mt: 5, p: 3, border: "1px solid #ccc", borderRadius: "5px" }}>
        <Typography variant="h4" gutterBottom>
          Giri Yap
        </Typography>
        {error && (
          <Alert severity="error" sx={{ mb: 2 }}>
            {error}
          </Alert>
        )}
        <form onSubmit={handleLogin}>
          <TextField
            label="E-posta"
            type="email"
            value={email}
            onChange={(e) => setEmail(e.target.value)}
            fullWidth
            margin="normal"
            required
          />
          <TextField
            label="ifre"
            type="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            fullWidth
            margin="normal"
            required
          />
          <Box sx={{ mt: 2 }}>
            <Button
              type="submit"
              variant="contained"
              color="primary"
              sx={{ mr: 2 }}
            >
              Giri Yap
            </Button>
            <Button
              component={Link}
              to="/register"
              variant="outlined"
              color="secondary"
            >
              Kayt Ol
            </Button>
          </Box>
        </form>
      </Box>
    </Container>
  );
};

export default LoginPage;
