import React from "react";
import {
  BrowserRouter as Router,
  Route,
  Routes,
  Link,
  useLocation,
} from "react-router-dom";
import LoginPage from "./components/LoginPage";
import RegisterPage from "./components/RegisterPage";
import AppointmentPage from "./components/AppointmentPage";

function App() {
  return (
    <Router>
      <header
        style={{
          backgroundColor: "#f8f9fa",
          padding: "10px",
          textAlign: "right",
        }}
      >
        <Link to="/login">
          <button style={{ padding: "10px 20px", marginRight: "10px" }}>
            Giri Yap
          </button>
        </Link>
        <CheckAuth /> {/* Oturum kontrol */}
      </header>

      <Routes>
        <Route path="/login" element={<LoginPage />} />
        <Route path="/register" element={<RegisterPage />} />
        <Route path="/appointment" element={<AppointmentPage />} />
        <Route path="/" element={<div>Ana Sayfa</div>} />
      </Routes>
    </Router>
  );
}

const CheckAuth = () => {
  const location = useLocation();
  const isAuthenticated = () => !!document.cookie.match("JSESSIONID");

  React.useEffect(() => {
    if (
      !isAuthenticated() &&
      location.pathname !== "/login" &&
      location.pathname !== "/register"
    ) {
      window.location.href = "/login"; // Backend oturumu yoksa ynlendirme
    }
  }, [location]);

  return null;
};

export default App;
